@media screen and(max-width: 1400px) {
    .container {
        width: 1100px;
    }
    .summary .summary__block .data .form__label .line {
        width: 340px;
    }
}
@media screen and(max-width: 1200px) {
    .container {
        width: 1000px;
    }
    .summary .summary__block .data .form__label .line {
        width: 310px;
    }
    .summary .summary__block .data .form__label .input {
        font-family: "Nunito";
        font-style: normal;
        font-weight: 400;
        font-size: 17px;
        line-height: 23px;
        color: #000000;
        width: 310px;
    }
    h1 {
        font-size: 46px;
    }
}
@media screen and(max-width: 1060px) {
    .container {
        width: 740px;
    }
    h1 {
        font-size: 40px;
    }
    .summary .summary__block .data .form__label .line {
        width: 230px;
    }
    .summary .summary__block .data .form__label .input {
        font-size: 16px;
        width: 210px;
    }
    .form__label {
        width: 100%;
    }
}
@media screen and(max-width: 800px) {
    .container {
        width: 540px;
    }
}
@media screen and(max-width: 576px) {
    .container {
        width: 85%;
    }
}
