@import "./components/header";
@import "zeroing";
@import "important";
@import "font-face";
@import "mixins";


.container {
    width: 1170px;
    margin: 0 auto
}
.section:nth-child(1) {
    padding-top: 60px;
    padding-bottom: 120px;
}
.summary {
    h1 {
        text-align: center;
    }
    p {
        text-align: center;
        font-family: 'Nunito';
        font-style: normal;
        font-weight: 600;
        font-size: 15px;
        line-height: 20px;
        color: #000000;
        padding-top: 20px;
    }
    .summary__block {
        padding-top: 60px;
        width: 100%;
        .data {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding-bottom: 30px;
            .form__label {
                .input {
                    font-family: 'Nunito';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 17px;
                    line-height: 23px;
                    color: #000000;
                    width: 350px;
                }
                .line {
                    margin-top: 5px;
                    border-bottom: 3px solid rgba(0, 0, 0, 0.7);
                    border-radius: 1px;
                    width: 370px;
                }
            }  
        }   
        .form__label {
            .textarea {
                font-family: 'Nunito';
                font-style: normal;
                font-weight: 300;
                font-size: 17px;
                line-height: 23px;
                color: #000000;
            }
            .line {
                width: 100%;
                border-bottom: 3px solid rgba(0, 0, 0, 0.7);
            }
        }
        .form__data {
            padding-top: 35px;
            .button {
                background: linear-gradient(180deg, #FE9E46 0%, #FFD74A 100%);
                box-shadow: 1px 1px 5px #FEC148;
                border-radius: 8px;
                font-family: 'Nunito';
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 135.4%;
                text-align: center;
                color: #FFFFFF;
                padding: 11px 44px;
            }
        }
    }
}


@import "./components/footer";
@import "breakpoints/breakpoint-summary";